<template>
  <div id="root-mobile">
    <main ref="main">
      <transition name="scale">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </transition>
    </main>

    <footer ref="footer">
      <nav class="nav-bar" v-show="isShowNavBar">
        <c-tab-bar v-model="currentView" @click="clickHandler" @change="goTo">
          <c-tab class="nav-item" v-for="(item,index) in tabs" :icon="item.icon" :label="item.label" :index="index" :key="item.label">
            <!-- name为icon的插槽 -->
            <i slot="icon" :class="['iconfont',`icon-${item.icon}`]">
            </i>
          </c-tab>
        </c-tab-bar>
      </nav>
    </footer>

  </div>
</template>
<script>
import { mapMutations } from "vuex";
let tabs = [
  {
    label: "主页",
    icon: "home",
    route: { name: "m.h" }
  },
  {
    label: "分类",
    icon: "iconfontchazhaofenlei",
    route: { name: "m.category" }
  },
  {
    label: "购物车",
    icon: "cart",
    route: { name: "m.trolley" }
  },
  {
    label: "个人中心",
    icon: "me",
    route: { name: "m.user" }
  }
];

export default {
  name: "mobileRoot",
  mounted() {
    this.setFooterHeight(this.$refs.footer.getBoundingClientRect().height);
    this.setCurrentView(tabs[0].label);
  },
  props: {},
  data() {
    return {
      isShowNavBar: true,
      isShowHomeHeader: true,
      transitionName: "",
      currentView: tabs[0].label,
      tabs
    };
  },
  computed: {
    // ...mapGetters("app.mobile", ["currentView"])
  },
  methods: {
    ...mapMutations("app.mobile", ["setFooterHeight", "setCurrentView"]),
    clickHandler(label, index) {
      console.log("clickHandler", label, index);
      console.log("main Rect", this.$refs.main.getBoundingClientRect());
    },
    goTo(label, index) {
      console.log("value has changed, now is", label, index);
      this.$router.push(this.tabs[index].route);
    }
  },
  components: {
    "c-button": () =>
      import(/* webpackChunkName: "c-container" */ "@cube/button/button"),
    "c-tab": () =>
      import(/* webpackChunkName: "c-container" */ "@cube/tab-bar/tab"),
    "c-tab-bar": () =>
      import(/* webpackChunkName: "c-container" */ "@cube/tab-bar/tab-bar"),
    "c-loading": () =>
      import(/* webpackChunkName: "c-container" */ "@cube/loading/loading")
  }
};
</script>

<style lang="scss">
#root-mobile {
  height: 100%;
  font-size: 1rem;
  overflow: hidden;
  display: flex;
  flex-flow: column nowrap;
  > header {
    height: 3rem;
  }
  > main {
    flex: 1;
    margin: auto 0;
  }
  > footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: white;
    > .nav-bar {
      height: 3rem;
      @include elevation2();
      .nav-item {
        position: relative;
        .iconfont {
          font-weight: 900;
          font-size: 1rem;
        }
        .label {
          margin-top: 0.1rem;
          font-size: 0.8rem;
          &::before {
            content: " ";
            position: absolute;
            // background: #000;
            // opacity: 0.3;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
          }
        }
      }
    }
  }
}
</style>
